import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import ValidateEmail from './validation/EmailValid';
import AnimateError from './validation/AnimateError';
import ValidateMobile from './validation/ValidateMobile';
import cities from './CitiesList';

export default function Account() {
    let navigate = useNavigate();
    const [userData, setUserData] = useState({
        name: '',
        email: '',
        mobile: '',
        city: ''
    });
    const [usrName, setUsrName] = useState('');
    const [usrEmail, setUsrEmail] = useState('');
    const [usrMobile, setUsrMobile] = useState('');
    const [usrCity, setUsrCity] = useState('');
    const [pageErr, setPageErr] = useState(false);
    const [proerr, setProerr] = useState({
        name: '',
        email: '',
        mobile: '',
        city: ''
    });
    const [disable, setDisable] = useState(false);
    const [srvErr, setSrvErr] = useState([]);
    const [proSucc, setProSucc] = useState(false);
    const arrangTabs = (proview, actview) => {
        document.getElementById(proview).classList.add("open");
        document.getElementById(actview).classList.add("active");
    }
    const clickTabs = (e) => {
        let actTab = document.getElementsByClassName("nav-link");
        let showTab = document.getElementsByClassName("profile_wrap");
        let getAt = e.target.getAttribute("data-target");
        actTab[0].classList.remove("active");
        actTab[1].classList.remove("active");
        actTab[2].classList.remove("active");
        showTab[0].classList.remove("open");
        showTab[1].classList.remove("open");
        showTab[2].classList.remove("open");
        e.target.classList.add("active");
        document.getElementById(getAt).classList.add("open");
    }
    useEffect(() => {
        arrangTabs("pro_det", "pro_act");
        let token = localStorage.getItem("token");
        if (token) {
            axios.get('api/userdetail', {
                headers: {
                    "x-access-token" : token,
                    "Content-Type" : "application/json"
                }
            }).then((res) => {
                setUserData(res.data);
                setUsrName(res.data.name);
                setUsrEmail(res.data.email);
                setUsrMobile(res.data.mobile);
                setUsrCity(res.data.city);
                // console.log(!isNaN(usrMobile+"sdss"), !isNaN(res.data.mobile));
            })
            .catch((err) => {
                setPageErr(true);
            });
        }
    }, []);
    const editProf = (e) => {
        e.preventDefault();
        // console.log(usrMobile);
        let errorCount = 0;
        if(usrName === '') {
            errorCount++;
            setProerr((prevState)=> {
                return {...prevState, name: 'Name is Required'}
            });
            AnimateError();
        } else if(usrName.length < 2){
            errorCount++;
            setProerr((prevState)=> {
                return {...prevState, name: 'Name must be 2 charecters'}
            });
            AnimateError();
        } else {
            setProerr((prevState)=> {
                return {...prevState, name: ''}
            });
        }  if(usrEmail === '') {
            errorCount++;
            setProerr((prevState)=> {
                return {...prevState, email: 'Email is Required'}
            });
            AnimateError();
        } else if(ValidateEmail(usrEmail)){
            errorCount++;
            setProerr((prevState)=> {
                return {...prevState, email: 'Email ID not valid'}
            });
            AnimateError();
        } else {
            setProerr((prevState)=> {
                return {...prevState, email: ''}
            });
        } if(usrMobile === '') {
            errorCount++;
            setProerr((prevState)=> {
                return {...prevState, mobile: 'Mobile Number Required'}
            });
            AnimateError();
        } else if(ValidateMobile(usrMobile)){
            errorCount++;
            setProerr((prevState)=> {
                return {...prevState, mobile: 'Mobile Number is not valid'}
            });
            AnimateError();
        } else {
            setProerr((prevState)=> {
                return {...prevState, mobile: ''}
            });
        }  if(usrCity === '') {
            errorCount++;
            setProerr((prevState)=> {
                return {...prevState, city: 'City Required'}
            });
            AnimateError();
        } else {
            setProerr((prevState)=> {
                return {...prevState, city: ''}
            });
        }
        if(errorCount === 0) {
            setDisable(true);
            let userData = { name: usrName, email: usrEmail, mobile: parseInt(usrMobile), city: usrCity };
            let token = localStorage.getItem("token");
            if (token) {
                axios.post('api/userupdate', userData, {
                    headers: {
                        "x-access-token" : token,
                        "Content-Type" : "application/json"
                    }
                }).then((res) => {
                    setProSucc(true);
                    setTimeout(function() {
                        setProSucc(false);
                    }, 2000);
                    setDisable(false);
                    setUserData({
                        name: usrName,
                        email: usrEmail,
                        mobile: usrMobile,
                        city: usrCity
                    })
                })
                .catch((err) => {
                    setSrvErr(err.response.data);
                    setDisable(false);
                })
            } else {
                setDisable(false);
                localStorage.removeItem("token");
                navigate("/login");
            }
        }
    }
    const [oldPass, setOldPass] = useState('');
    const [usrPass, setUsrPass] = useState('');
    const [confPass, setConfPass] = useState('');
    const [passerr, setPasserr] = useState({
        old: '',
        pass: '',
        conf: ''
    });
    const [passdisable, setPassDisable] = useState(false);
    const [pasSrvErr, setPasSrvErr] = useState([]);
    const [pasSucc, setPasSucc] = useState(false);

    const changePass = (e) => {
        e.preventDefault();
        let errorCount = 0;
        if(oldPass === '') {
            errorCount++;
            setPasserr((prevState)=> {
                return {...prevState, old: 'Old Password is Required'}
            });
            AnimateError();
        } else if(oldPass.length < 5){
            errorCount++;
            setPasserr((prevState)=> {
                return {...prevState, old: 'Password must have 6 charecters'}
            });
            AnimateError();
        } else {
            setPasserr((prevState)=> {
                return {...prevState, old: ''}
            });
        } if(usrPass === '') {
            errorCount++;
            setPasserr((prevState)=> {
                return {...prevState, pass: 'New Password is Required'}
            });
            AnimateError();
        } else if(usrPass.length < 5){
            errorCount++;
            setPasserr((prevState)=> {
                return {...prevState, pass: 'Password must have 6 charecters'}
            });
            AnimateError();
        } else {
            setPasserr((prevState)=> {
                return {...prevState, pass: ''}
            });
        } if(confPass === '') {
            errorCount++;
            setPasserr((prevState)=> {
                return {...prevState, conf: 'Enter Confirm Password'}
            });
            AnimateError();
        } else if(confPass !== usrPass) {
            errorCount++;
            setPasserr((prevState)=> {
                return {...prevState, conf: 'Password not match'}
            });
            AnimateError();
        } else {
            setPasserr((prevState)=> {
                return {...prevState, conf: ''}
            });
        }
        if(errorCount === 0) {
            setPassDisable(true);
            let userPassword = { oldPassword: oldPass, password: usrPass };
            let token = localStorage.getItem("token");
            if (token) {
                axios.post('api/userpassupdate', userPassword, {
                    headers: {
                        "x-access-token" : token,
                        "Content-Type" : "application/json"
                    }
                }).then((res) => {
                    setPassDisable(false);
                    setPasSucc(true);
                    setTimeout(function() {
                        setPasSucc(false);
                    }, 2000);
                })
                .catch((err) => {
                    setPassDisable(false);
                    setPasSrvErr(err.response.data);
                })
            } else {
                setPassDisable(false);
                localStorage.removeItem("token");
                navigate("/login");
            }
        }
    }
    const [citystr, setCitystr] = useState('');
    const [cityshow, setCityshow] = useState(false);
    const citySelect = (e) => {
        document.getElementById('city').value = e.target.getAttribute("name");
        setCityshow(false);
        setUsrCity(e.target.getAttribute("name"));
    }

    const cityString = (e) => {
        if(e.target.value.length > 2) {
            setCityshow(true);
        } else {
            setCityshow(false);
        }
        setCitystr(e.target.value);
        setUsrCity(e.target.value);
    }

    const filterCity = cities.filter((element) => {
        return element.toLowerCase().match(citystr.toLowerCase());
    });
    return (
        <>
            <section className="logged spad">
                {!pageErr?<div className="container">
                    <div className="inner_boxes">
                        <ul className="nav nav-pills nav-fill custom_nav_pills">
                            <li className="nav-item">
                                <a className="nav-link" id="pro_act" onClick={clickTabs} data-target="pro_det">Profile Details</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" id="edit_act" onClick={clickTabs} data-target="pro_edit">Edit Profile</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" id="chnge_act" onClick={clickTabs} data-target="change_pass">Change Password</a>
                            </li>
                        </ul>
                        <div className="profile_wrap" id="pro_det">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <small className="form-text text-muted">Name</small>
                                        <label>{userData.name?userData.name:""}</label>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <small className="form-text text-muted">E-mail</small>
                                        <label>{userData.email?userData.email:""}</label>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <small className="form-text text-muted">Mobile</small>
                                        <label>{userData.mobile?userData.mobile:""}</label>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <small className="form-text text-muted">City</small>
                                        <label>{userData.city?userData.city:""}</label>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="profile_wrap" id="pro_edit">
                            <form autoComplete="off" onSubmit={editProf}>
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <small className="form-text text-muted mb-2">Name</small>
                                            <div className="form_wr_input">
                                                <input className="form_input_style" onChange={(e)=>setUsrName(e.target.value)} defaultValue={userData.name} type="text" placeholder="Your name" />
                                                <span className="form_err">{proerr.name}</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <small className="form-text text-muted mb-2">E-mail</small>
                                            <div className="form_wr_input">
                                                <input className="form_input_style" type="text" onChange={(e)=>setUsrEmail(e.target.value)} defaultValue={userData.email} placeholder="E-mail" />
                                                <span className="form_err">{proerr.email}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <small className="form-text text-muted mb-2">Mobile</small>
                                            <div className="form_wr_input">
                                                <input className="form_input_style" type="text" onChange={(e)=>setUsrMobile(e.target.value)} defaultValue={userData.mobile} placeholder="10 Digit Phone Number" />
                                                <span className="form_err">{proerr.mobile}</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <small className="form-text text-muted mb-2">City</small>
                                            <div className="form_wr_input relative">
                                                <input className="form_input_style" id="city" type="text" onChange={cityString} defaultValue={userData.city} placeholder="City" />
                                                {(cityshow && filterCity.length !== 0) ? <div className="page_city_wrap">
                                                    {filterCity.map((filtcities) => <span className="page_city_list" key={filtcities} name={filtcities} onClick={citySelect}>{filtcities}</span>)}
                                                </div> : ""}
                                                <span className="form_err">{proerr.city}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-12">
                                        <button className="site-btn sb-big" disabled={disable}>SAVE</button>
                                    </div>
                                    <div className="col-sm-12 server_error">
                                        {srvErr && srvErr.map(err =>
                                            <div key={err.msg} className="form_err">{err.msg}</div>
                                        )}
                                        {proSucc?<p className="success_msge">Your Profile Updated Successfully</p>:""}
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div className="profile_wrap" id="change_pass">
                            <form onSubmit={changePass} autoComplete="off">
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <small className="form-text text-muted mb-2">Old Password</small>
                                            <div className="form_wr_input">
                                                <input className="form_input_style" type="password" onChange={(e)=>setOldPass(e.target.value)} placeholder="Old Password" />
                                                <span className="form_err">{passerr.old}</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <small className="form-text text-muted mb-2">New Password</small>
                                            <div className="form_wr_input">
                                                <input className="form_input_style" type="password" onChange={(e)=>setUsrPass(e.target.value)} placeholder="New Password" />
                                                <span className="form_err">{passerr.pass}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="form-group">
                                            <small className="form-text text-muted mb-2">Confirm New Password</small>
                                            <div className="form_wr_input">
                                                <input className="form_input_style" type="password" onChange={(e)=>setConfPass(e.target.value)} placeholder="Confirm New Password" />
                                                <span className="form_err">{passerr.conf}</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <small className="form-text text-muted mb-2">&nbsp;</small>
                                        <button disabled={passdisable} className="site-btn sb-big">SAVE</button>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-md-12">
                                        {pasSrvErr && pasSrvErr.map(err =>
                                            <div key={err.msg} className="form_err">{err.msg}</div>
                                        )}
                                        {pasSucc?<p className="success_msge">Password Updated Successfully</p>:""}
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>:<div className="container">
                    <div className="error-page text-center">
                        <div style={{height:"60px"}}>&nbsp;</div>
                        <h5 className="not_fnd_hd">OOPS... Something Went Wrong</h5>
                        <p className="not_fnd_body">
                            Try to Search some other Property<br />
                            <Link to="/" className="readmore-btn">Back to Home Page</Link>
                        </p>
                    </div>
                </div>}
            </section>
        </>
    )
}
